import styles from "./AboutPage.module.scss";
import React from "react";
import PropTypes from "prop-types";
import { PageTitle } from "../../components";
import {PAGE_TITLE_CONSTANT} from "../../constants";

const AboutPage = ({url,theme}) => {
    const renderIframe = (
        <iframe src={url} title='About AllSpark' frameBorder="0" allow='accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture' allowFullScreen/> 
    )
    return(
        <div className={styles.aboutContainer} >
            <PageTitle label={PAGE_TITLE_CONSTANT.ABOUT} />
            {/* {isLoading && renderLoader } */}
            {renderIframe}
        </div> 
    )
}

export default AboutPage;
AboutPage.propTypes = {
    url : PropTypes.string,
    theme : PropTypes.string
}